import React, { PureComponent } from "react";
import {ENTER_KEY, TAB_KEY} from 'react-datasheet/lib/keys';
import Select from 'react-select';


export default class SelectMeasureEditor extends PureComponent {
    constructor (props) {
      super(props)
      this.handleChange = this.handleChange.bind(this)
      this.state = {}
    }
  
    handleChange (opt) {
      const { onCommit, onRevert } = this.props
      // if (!opt) {
      //   return onRevert()
      // }
      const { e } = this.state
      onCommit(opt.target.value, e)
    }
  
    render () {
      return (
        <select
          defaultValue={1}
          onChange={this.handleChange}
        >
          {
            measures.map(measure=><option value={measure.value}> {measure.value} - {measure.label}</option>)
          }
        </select>
      )
    }
  }


